@import url(https://fonts.googleapis.com/css2?family=Grandstander:wght@700&family=Raleway:wght@900&display=swap);
body{background-color:#87a78e;margin:10px 0}.heading{text-align:center;font-family:'Raleway', sans-serif;font-size:60px}.container{display:flex;flex-direction:row;justify-content:space-evenly;align-items:stretch}.outer-window{width:700px;background-color:#b3c7b7;border:1px solid #29382d;box-shadow:1px 1px 10px 2px #5f8267;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.outer-window .window-heading{position:relative;background-color:#536e58;padding:4px 4px 3px 3px;font-family:'Grandstander', cursive;font-size:25px;border:1px solid #29382d;margin:0px;text-align:center;border:1px solid #29382d;box-shadow:1px 1px 10px 2px #5f8267}#editor{width:98%;height:90%;border:1px solid #29382d;box-shadow:1px 1px 10px 2px #5f8267}#preview{width:99%;height:94%;border:1px solid #29382d;box-shadow:1px 1px 10px 2px #5f8267;background-color:#d0ddd3}

